


















import {Vue , Component} from 'vue-property-decorator'

@Component({})
export default class TechnologyList extends Vue{
    [x: string]: any
    data={
        watch:0,
        love:0,
        comment:{}
    }
    field={
        _id:{ label:"ID" },
        cover:{ label:'背景图' },
        title:{ label:'标题' },
        category:{ label:'分类' },
        watch:{ label:'观看量' },
        text:{ label:'富文本' },
        love:{ label:'小心心' },
        comment:{ label:'评论' },
        head:{ label:'头像' },
        name:{ label:'作者' }
    }
    async fetch(){
        const res = await this.$http.get('technology')
        this.data = res
    }
    async remove(row:any){
        await this.$confirm('是否确认删除？')
        await this.$http.delete(`technology/${row}`)
        this.$message.success('删除成功')
        this.fetch()
    }
    created(){
        this.fetch()
    }
}
